import { useEffect } from "react";
import $ from 'jquery';

function Contact() {
    useEffect(() => {
        let goTop=document.querySelector(".go_topbtn");
        goTop.addEventListener("click",function(e){
            e.preventDefault();
            $('html, body').animate({
                scrollTop : 0
            });
            return false;
        });
    });
    return (
    <div id="contact">
        <div className="ft_inner">
            <div className="ft_left">
                <h3>내일의 경험을 만든다</h3>
            </div>
            <div className="ft_right">
                <ul>
                    <li>
                        <p>Tel</p>
                        <span>010-3038-6598</span>
                    </li>
                    <li>
                        <p>E-mail</p>
                        <span>wlsdn0622@naver.com</span>
                    </li>
                </ul>
                <p className="adress">
                    <strong>Location</strong>
                    경기도 수원시 장안구 천천동 베스스타운@714동 1603호
                </p>
                <p className="name">©Limjinwoo</p>
                <p className="copyright">All rights reserved</p>
            </div>
        </div>
        <div className="top_btn">
            <a href="" className="go_topbtn" />
        </div>
    </div>
);
}

export default Contact;